.detail {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      padding-bottom: 100%;
      height: 0;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    @include hover(transform,scale(1.05));
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__text {
    @include pageGrid;
    row-gap: 8px;
  }

  &__title {
    grid-row: 1;
    grid-column: 1 / -1;

    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
  }

  &__description {
    grid-row: 2;
    grid-column: span 2;

    font-size: 16px;
    line-height: 22.4px;

    @include onTablet {
      grid-column: span 6;
    }

    @include onDesktop {
      grid-column: span 8;
    }
  }
}
